import React, {ReactElement, useEffect, useState} from 'react';
import Popup from 'reactjs-popup';
import classNames from 'classnames';
import {CircleSlider} from 'react-circle-slider';
import './halfGOptimalProportion.css';
import {useHistory} from 'react-router-dom';

export default function HalfGOptimalProportion(): ReactElement {
  const [sliderValue, setSliderValue] = useState(0);
  const [error, setError] = useState('');

  const history = useHistory();

  const handleSubmit = () => {
    localStorage.setItem('savingsPercent', JSON.stringify(sliderValue));
    localStorage.setItem('fondsPercent', JSON.stringify(100 - sliderValue));

    history.push('/percent-calculation');
  };

  useEffect(() => {
    if (100 - sliderValue >= 60) {
      setError(
        'Attention, Vous avez un pourcentage en fonds risqu. La partie en fond peut tre perdue.',
      );
    } else {
      setError('');
    }
  }, [sliderValue]);

  const handleSliderChange = (e: number) => {
    console.log(e);
    setSliderValue(e);
  };

  return (
    <div className="half-optimal-proportion">
      <div className="half-optimal-proportion_text">
        Pour la solution mi-garantie/mi-rendement, vous dfinissez une partie
        garantie (100% scurise, mais avec un taux de 0.1%) et une partie en
        fonds (non garantie mais avec un rendement plus intressant).
      </div>

      <div className="half-optimal-proportion_percents">
        <div className="proportion_percent">
          <div className="percent gold_text">{`${sliderValue}%`}</div>
          <div className="percent-desc">Epargne</div>
        </div>

        <div className="circle_button">
          <CircleSlider
            size={260}
            progressWidth={10}
            circleWidth={10}
            progressColor={'white'}
            stepSize={10}
            value={sliderValue}
            onChange={handleSliderChange}
          />

          <div className="circle"></div>
        </div>

        <div className="proportion_percent">
          <div className="percent gold_text">{`${100 - sliderValue}%`}</div>
          <div className="percent-desc">Fonds</div>
        </div>
      </div>

      {error && (
        <div className="error_block_position">
          <img src="/image/error.png" className="error_img" alt="error" />
          {error}{' '}
        </div>
      )}

      <button onClick={handleSubmit} className="next_button button_position">
        Continuer
      </button>

      <Popup
        modal
        trigger={
          <div className="pop_up_triger">
            Quelle est la proportion optimale ?
          </div>
        }>
        {(
          close:
            | ((event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void)
            | undefined,
        ) => (
          <>
            <button className="close" onClick={close}>
              X
            </button>
            <div className="pop_up">
              <div className="pop_up_title">
                Quelle est la proportion optimale ?
              </div>
              <div className="pop_up_text">
                Il nexiste pas de choix optimal par dfaut. La rpartition
                dpend fortement de votre propension au risque.
              </div>
              <ul className="pop_up_list">
                <li className="pop_up_list-item">
                  La partie  capital garanti  est identique  la solution
                  garantie, une pargne classique avec un taux dintrt trs
                  faible.{' '}
                </li>
                <li className="pop_up_list-item">
                  La partie fonds de placement est plus risque, mais peut avoir
                  un retour sur investissement beaucoup plus lev.{' '}
                </li>
              </ul>
              <div className="pop_up_text">
                Cette solution est la solution recommande pour la plupart des
                profils d'investissement, car elle permet un rendement plus
                intressant que le capital garanti sans exposer  un risque trop
                lev. Le taux de 0.20% sur la partie garantie est indicatif et
                dpend du type produit, de la compagnie d'assurance ou de la
                banque et des conditions de march.
              </div>
            </div>
          </>
        )}
      </Popup>
    </div>
  );
}
