import React, { ReactElement, useState } from "react";
import { useHistory } from "react-router-dom";
import Popup from "reactjs-popup";
import "./occupation.css";

export default function Occupation(): ReactElement {
  const [occupation, setOccupation] = useState("");

  const history = useHistory();

  const handleSubmit = () => {
    localStorage.setItem("occupation", occupation);
    if (occupation === "Salari") {
      return history.push("/employee");
    } else if (occupation === "Indpendant") {
      return history.push("/self-employed");
    }
  };

  return (
    <div className="occupation">
      <div className="occupation_title">
        Afin de savoir jusqu' quel montant vous pouvez verser annuellement,
        nous devons savoir si vous tes :
      </div>

      <div className="occupation_inputs">
        <div className="occupation_radio_button">
          <input
            name="occupation"
            value={occupation}
            onChange={(e) => {
              setOccupation("Salari");
            }}
            id="input_employee"
            type="radio"
          />
          <label className="occupation_label" htmlFor="input_employee">
            Salari
        </label>
        </div>

        <div className="occupation_radio_button">
          <input
            name="occupation"
            value={occupation}
            onChange={(e) => {
              setOccupation("Indpendant");
            }}
            id="input_independent"
            type="radio"
          />
          <label className="occupation_label" htmlFor="input_independent">
            Indpendant
        </label>
        </div>

      </div>

      <button
        disabled={occupation === ""}
        onClick={handleSubmit}
        className="next_button button_position"
      >
        Continuer
      </button>

      <Popup
        modal
        trigger={<div className="pop_up_triger">Quelle est la diffrence?</div>}
      >
        {(
          close:
            | ((event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void)
            | undefined
        ) => (
          <>
            <button className="close" onClick={close}>
              X
            </button>
            <div className="pop_up">
              <div className="pop_up_title">
                Troisime pilier: les diffrences entre salari et indpendant
              </div>
              <div className="pop_up_text">
                Il est important pour nous de savoir si vous tes salari ou
                indpendant car la cotisation au 3me pilier est diffrente.{" "}
              </div>
              <div className="pop_up_text">
                Dans le cas dun salari, vous pouvez conomiser jusqu 6883
                CHF par an.
              </div>
              <div className="pop_up_text">
                Dans le cas dun indpendant, comme le 2me pilier nest pas
                obligatoire, sil dcide de ne pas y souscrire, il a la
                possibilit de verser 34416 CHF ou jusqu 20% de son salaire
                AVS dans un 3me pilier. Si un indpendant est affili au 2me
                pilier, la cotisatin maximale de son 3me pilier sera la mme
                que celle dun salari.
              </div>
            </div>
          </>
        )}
      </Popup>
    </div>
  );
};
